.smtrContainer {
    width: 1850px;
    height: 870px;
    overflow-x: scroll;
    overflow-y: scroll;
    background-color: #090909;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.smtrHeader {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    font-weight: bold;
    font-size: small;
    text-align: center;
    height: 40px;
    width: fit-content;
}

.smtrItem {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    text-align: center;
    height: 50px;
    width: fit-content;
}

.item {
    background-color: #C0C0C0;
    border: solid 1px rgba(0, 0, 0, 0.3);
    color: #000000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: small;
}