.tasksPanel {
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;
    width: 280px;
    background-color: #090909;
} 

.tasksPanelMinimized {
    width: 277px;
    height: 57px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    /* background-color: #090909; */
    font-size: large;
    cursor: pointer;
    user-select: none;
    border-left: solid 3px white;
    border-bottom: solid 3px white;
}

.tasksContainer {
    border-left: solid 3px white;
    height: 93.7vh;
    /* width: 100%; */
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.taskContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 20px;
    border-top: solid 1px white;
    border-bottom: solid 1px white;
    width: 100%;
    height: 200px;
    color: white;
    text-align: center;
}

.taskName {
    font-size: x-large;
}
